<template lang="html">
<!-- <div class="room-wrapper">
  <a class="room-title">王者荣耀<span class="more">更多</span></a>
  <div class="room-list">
    <div class="room-item" v-for="room in roomList">
      <div class="room-bg">
        <img :src="room.room_src" alt="" class="room-image">
        <span class="room-online">{{room.online}}</span>
        <span class="room-nickname">{{room.nick_name}}</span>
      </div>
      <p class="room-name">{{room.room_name}}</p>
    </div>
  </div>
</div> -->
<roomlist :room-list="roomList"></roomlist>
</template>

<script>
import roomlist from '../global/roomlist'
export default {
  data(){
    return{
      gameList:[],
      roomList:[]
    }
  },
  created(){
    this.$http.get('http://open.douyucdn.cn/api/RoomApi/game').then((res)=>{
      res = res.data;
      this.gameList = res.data;
    });
    this.$http.get('http://open.douyucdn.cn/api/RoomApi/live').then((res)=>{
      res = res.data;
      this.roomList = res.data;

      console.log(this.roomList)
    })
  },
  components:{
    roomlist
  }
}
</script>

<style lang="stylus">
.room-wrapper
  width 100%
  height 100%
  font-size 0
  .room-title
    display block
    position relative
    height 30px
    line-height 30px
    font-size 18px
    padding-left 10px
    color #000
    .more
      position absolute
      right 10px
      font-size 12px
  .room-list
    display flex
    flex-wrap wrap
    width 100%
    .room-bg
      .room-image
        width 140px
        height 100px

</style>



